$primary: #195f76;
$alternate: #2ba4cb;
$secondary: #722bcb;

$white: #f2f2f2;
$black: #071b21;

.light-theme {
    color: $black;
    background-color: $white;

    a,
    a:visited,
    a:active,
    a:hover {
        color: $black;
    }

    .menu-animation {
        background-color: $primary;
    }

    nav {
        background-color: $white;
        color: $black;

        border-bottom: 1px solid $black;

        a,
        a:visited,
        a:active,
        a:hover,
        .dropdown-toggle.nav-link {
            color: $black;
        }

        .nav-item.dropdown {
            background-color: $white;
            color: $black;
        }

        a.navbar-brand {
            color: $black;
        }

        .dropdown-menu,
        .navbar-toggler {
            color: $black;
            background-color: $white;
            border-color: $black;
        }

        .navbar-toggler-icon {
            color: $white;
        }

        .dropdown-item:hover {
            background-color: $primary;
            color: $white;
        }
    }
}