@import url(https://fonts.googleapis.com/css?family=Lato);
@import url("https://fonts.googleapis.com/css?family=Merriweather:400,400i,700");

$sans-serif: Lato, sans-serif;
$serif: Merriweather;

* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: $sans-serif;
}

@import url("./dark-theme.scss");
@import url("./light-theme.scss");

.middlecenter-container {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    min-width: 100vw;
    position: relative;
}

.loading {
    font-family: $serif;
    font-size: 5rem;
    top: 30vh;
    position: absolute;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.content {
    width: 80%;
    height: 100vh;
    text-align: center;
}


.menu-animation, .content-animation {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 50px;
    width: 50px;
    opacity: 1;
    position: absolute;
}